.h2 {
	font-weight: bold;
	margin: 0;
}

.h2 {
	text-align: center;
	margin-bottom: 7px;
}

.p {
	font-size: 14px;
	font-weight: 100;
	line-height: 20px;
	letter-spacing: 0.5px;
	margin: 20px 0 30px;
}

.span {
	font-size: 12px;
}

.a {
	color: #333;
	font-size: 14px;
	text-decoration: none;
	margin: 15px 0;
}

.button {
	border-radius: 20px;
	border: 1px solid white;
	background-color: #6739b7;
	color: #FFFFFF;
	font-size: 12px;
	font-weight: bold;
	padding: 12px 45px;
	letter-spacing: 1px;
	text-transform: uppercase;
	transition: transform 80ms ease-in;
	cursor: pointer;
	margin-top: 10px;
}

.button:hover {
	border-radius: 20px;
	border: 2px solid white;
	background-color: #681aef;
	color: #FFFFFF;
	font-size: 12px;
	font-weight: bold;
	padding: 12px 45px;
	letter-spacing: 1px;
	text-transform: uppercase;
	transition: transform 80ms ease-in;
	cursor: pointer;
	margin-top: 10px;
}


.disabled_button {
	border-radius: 20px;
	border: 1px solid white;
	background-color: #cccccc;
	color: #FFFFFF;
	font-size: 12px;
	font-weight: bold;
	padding: 12px 45px;
	letter-spacing: 1px;
	text-transform: uppercase;
	transition: transform 80ms ease-in;
	cursor: pointer;
	margin-top: 10px;
}

.button:active {
	transform: scale(0.95);
}

.button:focus {
	outline: none;
}

.button.ghost {
	background-color: transparent;
	border-color: #FFFFFF;
}

.form {
	background-color: #FFFFFF;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 0 50px;
	height: 100%;
	text-align: center;
	margin-top: 10px;
}

.input {
	background-color: #eee;
	border: none;
	padding: 12px 15px;
	margin: 8px 0;
	width: 100%;
    border-radius: .3rem;
}

.signup_container {
	background-color: #fff;
	border-radius: 10px;
  	box-shadow: 0 14px 28px rgba(0,0,0,0.25), 
			0 10px 10px rgba(0,0,0,0.22);
	position: relative;
	overflow: hidden;
	width: 780px;
	max-width: 100%;
	min-height: 520px;
    margin: 1rem auto;
}

.formContainer {
	position: absolute;
	top: 0;
	height: 100%;
}




.signup_container.rightPanelActive .signUpContainer {
	transform: translateX(100%);
	opacity: 1;
	z-index: 50;
    width: 50%;
}



.overlayContainer {
	position: absolute;
	top: 0;
	left: 0%;
	width: 50%;
	height: 100%;
	overflow: hidden;
	z-index: 1;
    opacity: 1;
    z-index: 10;
}

.overlay {
	background: linear-gradient(to right, #b194e3, #6739b7);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 0 0;
	color: #FFFFFF;
	position: relative;
	left: 0%;
	height: 100%;
	width: 100%;
    z-index: 14;
    opacity: 1;
 
}

.overlayPanel {
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 0 40px;
	text-align: center;
	top: 0;
	height: 100%;
	width: 100%;
    left: 0%;
    z-index: 50;
    opacity: 1;
}


.socialContainer {
	margin: 20px 0;
}

.socialContainer a {
	border: 1px solid #DDDDDD;
	border-radius: 50%;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	margin: 0 5px;
	height: 40px;
	width: 40px;
}

.errorMessage {
	color: red;
	font-size: 14px;
	margin: 0px;
	padding: 0px;
  }

  .error {
	border-color: red;
  }

 
  @media (max-width: 792px) {
	.overlayContainer {
	  display: none;
	}

	.signup_container {
		width: 80%;
	}

	.signUpContainer {
		left: -50%;
	}

	.form {
		width: 200%
		
	}


  }


.passwordcontainer {
	position: relative;
	width: 100%;
}
  
.passwordtoggle {
	position: absolute;
	top: 50%;
	right: 10px;
	transform: translateY(-50%);
	cursor: pointer;
}

.accept_terms {
	margin-bottom: 0px;
	height: 3vh;
	padding-top: 0px;
	/* font-size: 15px; */
}